.carousel {
  position: relative;
  width: 600px;
  height: 500px;
  overflow: hidden;
  margin: 0 auto; }

.slide {
  float: right;
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
  background-color: #eee;
  text-align: center;
  -webkit-transition: opacity 0.4s;
  transition: opacity 0.4s;
  opacity: 1; }
  .slide:before {
    content: attr(annot);
    display: block;
    position: absolute;
    left: 20px;
    bottom: 20px;
    color: rgba(255, 255, 255, 0.9);
    font-size: 14px;
    font-weight: 300;
    z-index: 12;
    opacity: 0;
    -webkit-transition: opacity 0.3s;
    transition: opacity 0.3s; }
  .slide:after {
    content: attr(slide);
    display: block;
    position: absolute;
    bottom: 0;
    -webkit-transition: opacity 0.3s;
    transition: opacity 0.3s;
    width: 100%;
    height: 80px;
    opacity: 0;
    text-align: left;
    text-indent: 549px;
    line-height: 101px;
    font-size: 13px;
    color: rgba(255, 255, 255, 0.9); }
  .slide img {
    width: 100%; }

.counter {
  display: none; }

.faux-ui-facia {
  top: 0;
  right: 0;
  float: right;
  position: absolute;
  margin-top: 0;
  z-index: 9;
  height: 100%;
  width: 100%;
  opacity: 0;
  cursor: pointer; }
  .faux-ui-facia:checked {
    z-index: 8; }
    .faux-ui-facia:checked + .slide {
      opacity: 0; }
    .faux-ui-facia:checked:nth-child(1):checked {
      z-index: 9;
      float: left;
      z-index: 9; }
      .faux-ui-facia:checked:nth-child(1):checked + .slide {
        opacity: 1; }
      .faux-ui-facia:checked:nth-child(1):checked ~ .faux-ui-facia {
        float: left;
        z-index: 8; }
        .faux-ui-facia:checked:nth-child(1):checked ~ .faux-ui-facia + .slide {
          opacity: 0; }
        .faux-ui-facia:checked:nth-child(1):checked ~ .faux-ui-facia:checked {
          z-index: 9; }
          .faux-ui-facia:checked:nth-child(1):checked ~ .faux-ui-facia:checked + .slide {
            opacity: 1; }

